:root {
  --blue: #1e90ff;
  --body-bg: #eeeeee;
  --white: #ffffff;
  --btn-gradient-bg: linear-gradient(to right, #1e90ff 0%, #12d8fa 51%, #1fa2ff 100%);
  --link-bg: #1e90ff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: #f0f0f0;
  overflow-x: hidden;
}
a
{
  text-decoration: none;
  color: black;
}
.navbar
{
    display: flex;
    justify-content: space-between;
    height: 10vh;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    z-index: 100;
    padding: .5rem 5rem 1rem 2rem;
   
}
.basket{
    position: relative;
}
.basket__icon{
    width: 35px;
}
.username{
    font-size: 1rem;
    pointer-events: none;
}
nav .cart__total {
    display: inline-block;
    text-align: center;
    
    font-size: 1rem; 
    margin: 2px;
    position: absolute;
    right: 40%;
    top: 30%;
    color: #fff;
}

.active {
    color: #ffffff;
    background-color: var(--blue);
}
.inactive {
    background-color: transparent;
}

a {
    color: #000;
}

.navbar__links
{
    display: flex;
    align-items: center;
}

.navbar__links > *
{
    text-decoration: none;
    padding: 0.5rem 1rem;
    border-radius: 15px;
    margin-left: 20px;
    text-transform: uppercase;
    font-weight: 600;
    position: relative;
    cursor: pointer;
    transition: all .5s ease-out;

}

.logo
{
    display: inline;
    outline: none;
   height: 7vh;
}
/* .navbar__links > *:hover::after
{
    width: 90%;
    color: #be3d68;
} */

.navbar__logo span
{
    display: block;
    font-size: 12px;
    text-align: center;
}
.navbar__logo
{
    display: flex;
    font-size: 12px;
    justify-content: space-between;
    align-items: center;
    
}
.logo__heading
{
    display: inline;
}
.navigation__text
{

    text-decoration: none;
    color: black;
}
.navbar__links > *:hover {
    background-color: var(--blue);
    color: #fff;
}
.nav-colored {
    background-color: #ebebeb;
}
.nav-transparent {
    background-color: transparent;
}
.display
{
    display: flex;
    flex-direction: column;

}
.hamburger 
{
    padding: 10px;
    display: none;
}
.hamburger :hover
{
    cursor: pointer;
}
.hamburger > span
{
    background-color: black;
    width: 40px;
    height: 5px;
    margin-bottom: 4px;
    display: block;
}
.brand
{
    display: flex;
}
.cart__total
{
    height: 100px;
}
@media only screen and (max-width:900px)
{
    .hamburger
    {
        display: inline;
        display: initial;
    }
    .navbar__links
    {
        display: none;
    }
    .display
    {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: #f0f0f0;
        width: 100vw;
    }
    .navbar
    {
        display: flex;
        flex-direction: column;
        padding: 10px 0px
    }
    nav .cart__total {
        width: 100%;
        right: -4%;
    }

}

