@media only screen and (min-width: 800px) and (max-width : 1000px)
{}
.cart__item {
    display: grid;
    grid-template-columns: 1fr 4fr 3fr 1fr;
    grid-template-rows: auto;
    place-items: center;
    align-items: center;
    margin-bottom: 2rem;
    padding: 1.2rem;
    background-color: #ffffff88;
    box-shadow: 0px 10px 0px 3px rgba(169, 169, 169, 0.144);
}
.cart__item__img > img {
    width: 200px;
    border-radius: 10px;
}

.cart__item .cart__item__name {
    text-align: start;
    color: #000;
    font-size: 1rem;
    text-transform: capitalize;
    font-weight: 600;
    padding-bottom: 1rem;
}

.variant {
    box-shadow: 1px 1px 10px 6px rgba(134, 134, 134, 0.144);
    border: 1px solid var(--blue);
    outline: none;
    margin-bottom: 1.5rem;
    display: flex;
    justify-content: space-between;
    font-size: 0.8rem;
    padding: 5px 10px;
    text-align: start;
    width: 100%;
    cursor: pointer;
    border-radius: 4px;
    color: black;
    transition: all 0.3s ease-in-out;
    position: relative;
    background-color: transparent;
}
.variant:hover {
    background: rgb(77, 145, 233);
    color: #fff;
}
.input__label {
    margin-left: 5px;
}
.variant__price h3 {
    cursor: pointer;
    background-color: transparent;
}
.variant__quantity h3 {
    font-style: italic;
    font-size: 0.75rem;
    margin-top: 2px;
    cursor: pointer;
    background-color: transparent;
    font-weight: 500;
}
.cart__item__bottom {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid transparent;
    box-shadow: 1px 1px 10px 10px rgba(134, 134, 134, 0.144);
    width: 8vw;
}
.cart__item__bottom > .subtract {
    padding: 0px 10px;
    border: 1px solid transparent;
    border-right: 1px solid rgba(0, 0, 0, 0.193);
    font-size: 1.5rem;
    align-items: center;
    outline: none;
    cursor: pointer;
}
button.add {
    cursor: pointer;
    padding: 0px 10px;
    border: 1px solid transparent;
    border-left: 1px solid rgba(0, 0, 0, 0.193);
    font-size: 1.5rem;
    outline: none;
    align-items: center;
}
.cart__item__bottom > .subtract:hover,
button.add:hover {
    color: var(--blue);
}

.cart__item__bottom input[type='number'] {
    border-radius: 5px;
    text-align: center;
    width: 60px;
    text-align: center;
    border: none;
    background: transparent;
    outline: none;
    font-weight: bolder;
}
input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
.cart__item__delete {
    border: 1px solid transparent;
    outline: none;
    padding: 3px;
    background: #efefef;
    cursor: pointer;
    transition: all 0.3s ease-out;
}
.cart__item__delete:hover {
    transform: scale(1.3);
}
.cart__item__delete > img {
    width: 20px;
}

@media screen and (min-width: 521px) and (max-width: 770px) {
    .cart__item {
        grid-template-columns: repeat(3, 1fr);
    }
    .cart__item__bottom {
        width: auto;
        width: initial;
    }
    .cart__product__price__group {
        grid-area: 1/2/2/-1;
        width: 90%;
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
    }
    .variant {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }

    .cart__item__img > img {
        width: 150px;
    }
}
@media only screen and (max-width: 520px) {
    .cart__item {
        grid-template-columns: 1fr;
        width: 80vw;
        margin-left: 2rem;
    }
    .cart__product__price__group {
        width: 75%;
        display: flex;
        flex-wrap: wrap;
        margin: auto;
    }
    .cart__item__img > img {
        width: 90%;
    }
    .variant {
        width: 45%;
        padding-left: 2px;
        padding-right: 4px;
        justify-content: center;
    }
    .variant:nth-of-type(even) {
        margin-left: 10px;
    }
    .cart__item__bottom {
        width: 130px;
        margin: 1rem 0;
        background: #efefef;
    }
    .cart__item .cart__item__name {
        text-align: center;
    }
}

